#base {
    .app__wrapper {
      height:50vh;
      justify-content: space-between;
    }

    .navDots {
        display: none;
        margin-left: -3rem;
      }
  
      .socialMedia {
        display: none;
      }
  }
  


  .app__base-brands {
    width: 80%;
    flex-wrap: nowrap;
    margin-top: 2rem;
    
  
    div {
      width: 150px;
      margin: 1.5rem;
  
      img {
        width: 100%;
        height: auto;
        object-fit: cover;
        filter: grayscale(1);
      }
  
      &:hover {
        img {
          filter: grayscale(0);
        }
      }
  
      @media screen and (min-width: 2000px) {
        width: 210px;
        margin: 2rem;
      }
  
      @media screen and (max-width: 450px) {
        width: 120px;
        margin: 1rem;
      }
    }
  
    @media screen and (max-width: 800px) {
      width: 100%;
    }
  }